import React, { useState, useEffect } from 'react';
import { fetchDailyData } from '../../api';
import { Line, Bar } from 'react-chartjs-2';
import styles from "./Chart.module.css";

const Chart = ({ data: { confirmed, recovered, deaths }, country }) => {
    const [dailyData, setDailyData] = useState({});

    useEffect(() => {
        const fetchMyAPI = async () => {
        const initialDailyData = await fetchDailyData();
        setDailyData(initialDailyData);
        };

        fetchMyAPI();
    }, []);

    const barChart = (
        confirmed ? (
            <Bar
                data={{
                    labels: ['Infected','Recovered', 'Deaths'],
                    datasets: [{
                        label: 'People',
                        backgroundColor: ["red", "green","grey"],
                    data:[confirmed.value, recovered.value, deaths.value],
                },
                ], 
            }}
            options={{ 
                legend:{ display:false },
                title : { display:true,  text :`Current State of COVID-19 in ${country}`},
            }}
            />
        ) : null
    );
                
    const lineChart = (
        dailyData[0]
            ? (
            <Line
            data={{
                labels: dailyData.map(({ date }) => date),

                datasets: [{
                    data: dailyData.map(( data ) => data.confirmed),
                    label: 'Infected',
                    backgroundColor: 'rgba(249, 19, 3, 0.824)',
                    borderColor: 'rgba(249, 19, 3, 0.824)',
                    pointRadius:2,
                    fill: '1',
                }, {
                    data: dailyData.map(( data ) => data.deaths),
                    label: 'Deaths',
                    borderColor: ' rgb(143, 139, 139)',
                    backgroundColor: 'rgb(143, 139, 139)',
                    fill: true, 
                    pointRadius:2,
                },
            ],
        }}
    />) : null
    );


    return (
        <div className={styles.container}>
            {country ? barChart : lineChart}
        </div>
    );
};

export default Chart
